import { initializeApp } from "firebase/app";
import { getAuth, signInWithEmailAndPassword, createUserWithEmailAndPassword, signOut, onAuthStateChanged } from "firebase/auth";
import * as firebaseui from 'firebaseui'; // Correctly import firebaseui

// Firebase configuration
const firebaseConfig = {
    apiKey: "AIzaSyBbHiDHVZCo2Pcym4FFICI2KBOsgPK9Pu8",
    authDomain: "peeval-ada50.web.app",
    projectId: "peeval-ada50",
    storageBucket: "peeval-ada50.appspot.com",
    messagingSenderId: "877270926609",
    appId: "1:877270926609:web:d4a1201bca6aecc371f8b0",
    measurementId: "G-KY3MZB7FG9"
  };

// Initialize Firebase
const app = initializeApp(firebaseConfig);
const auth = getAuth(app);

// Initialize FirebaseUI
const ui = new firebaseui.auth.AuthUI(auth);

// Configure FirebaseUI
const uiConfig = {
    signInSuccessUrl: '/home', // URL de redirecionamento após login bem-sucedido
    signInOptions: [
        firebaseui.auth.MicrosoftAuthProvider.PROVIDER_ID,
        firebaseui.auth.GoogleAuthProvider.PROVIDER_ID,
        firebaseui.auth.EmailAuthProvider.PROVIDER_ID,
    ],
    tosUrl: '/terms-of-service', // Termos de serviço
    privacyPolicyUrl: '/privacy-policy' // Política de privacidade
};

// Function to start FirebaseUI
export const startFirebaseUI = (elementId) => {
    ui.start(elementId, uiConfig);
};

// Authentication functions
export const signIn = (email, password) => signInWithEmailAndPassword(auth, email, password);
export const signUp = (email, password) => createUserWithEmailAndPassword(auth, email, password);
export const logout = () => signOut(auth);
export const onAuthStateChange = (callback) => onAuthStateChanged(auth, callback);

// Exporting auth as the default export and app as a named export
export { app };
export default auth;